.login {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 600px;
  margin-bottom: 200px;
  align-items: center;
  text-align: center;
}

.register {
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 150px;
  margin-top: 150px;
}

.connect {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 200px;
  align-items: center;
  text-align: center;
}

.input-form {
  width: 500px;
  height: 35px;
  font-size: 30px;
  margin-left: auto;
}

h3 {
  font-size: 2rem;
  color: white;
}
h2 {
  font-size: 1.5rem;
  color: white;
}

#incorrect {
  margin-top: 25px;
}

.label-form {
  font-size: 50px;
  margin-bottom: 20px;
}

.button-form {
  background-color: transparent;
  color: #fff;
  padding: 21px 40px;
  border: 1px solid;
  transition: all 0.3s ease-out;
  font-size: 25px;
  margin-top: 25px;
}

.button-save {
  background-color: transparent;
  color: #fff;
  padding: 21px 40px;
  border: 1px solid;
  transition: all 0.3s ease-out;
  font-size: 25px;
  margin-top: 25px;
  margin-top: 25px;
}

.button-register {
  background-color: transparent;
  color: #fff;
  padding: 21px 40px;
  border: 1px solid;
  transition: all 0.3s ease-out;
  font-size: 25px;
  margin-top: 50px;
}

.button-disconnect {
  background-color: transparent;
  color: #fff;
  padding: 21px 40px;
  border: 1px solid;
  transition: all 0.3s ease-out;
  font-size: 25px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.button-form:hover,
.button-register:hover,
.button-disconnect:hover,
.button-save:hover {
  transition: all 0.3s ease-out;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.5);
  transition: 250ms;
}

#alert {
  font-size: 25px;
  color: red;
}
